const useW = () => {
  const [w, setW] = useState(undefined);
  const hasWindow = typeof window !== 'undefined';
  
  
  
  

  useEffect(() => {
    const root = document.getElementById('root');

    function calculateWidth() {
      return window.innerWidth > 600 ? 600 : root.getBoundingClientRect().width;
    }
    function handleResize() {
      setW(calculateWidth());
      document.getElementById('root').style.width = window.innerWidth > 600 ? '600px' : `${window.innerWidth}px`;
    }
    window.addEventListener("resize", handleResize);
    handleResize();
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  return hasWindow ? w : null;
}

function Home() {

  const myOrder = [
    0, 1, 2, 3,
    2, 3, 0, 1,
    1, 2, 3, 0,
    3, 0, 1, 2,
    2, 3, 0, 1,
    0, 1, 2, 3
  ];
  

  const isDayNotYet = idx => {
    const d = new Date();
    return d.getMonth()=== 10 && d.getDate()>idx;
  }

  const isDayToday = idx => {
    const d = new Date();
    return d.getMonth()=== 10 && d.getDate()===idx+1;
  }

  const w = useW();
  const list = Object.keys(SRC);
  
  const returnKeys = () => {
    return <Days>{list.map((key, idx) => {
      const className = isDayToday(idx) ? 'today' : undefined;
      return (
        <Day data-w={w} data-nr={myOrder[idx]} key={key}>
          {isDayNotYet(idx) ? <a href={`#/${key}`} className={className}>{idx+1}</a> : <span>{idx+1}</span>}
        </Day>
      )
    })}</Days>
  }

  return (
    <>
      <Advent data-w={w}>
        <AdventTop data-w={w}>
          <h1>Advent 2024</h1>
        </AdventTop>
      
        <ContentMain>
         {returnKeys()}    
        </ContentMain>
      
        <AdventBottom data-w={w}>
          <h3>Boldog Adventet</h3>
        </AdventBottom>
      </Advent>
    </>
  );
}

function AdventPage() {
  const htmlParser = new Parser();
  const { param } = useParams();
  const obj = SRC[param];
  const w = useW();
  if(!obj) {
    return <></>
  }

  return (
    <>
      <Advent data-w={w}>
        <AdventTop data-w={w}>
          <LinkBack href="#/">&#60;</LinkBack>
          <Datum>{obj.date}</Datum>
          <h2>{htmlParser.parse(obj.title)}</h2>
        </AdventTop>
      
        <Content>
          {htmlParser.parse(obj.story)}
        </Content>
      
        <AdventBottom data-w={w}>
          <h3>{htmlParser.parse(obj.quote)}</h3>
        </AdventBottom>
      </Advent>
    </>
  )
}

function App() {
  return (
    <HashRouter>
        <Routes>
          <Route path="/:param" element={<AdventPage />} />
          <Route path="/" element={<Home />} />
        </Routes>
    </HashRouter>
  );
}

export default App;